import Glide from '@glidejs/glide';
import { TweenMax, Expo } from 'gsap';
import Rellax from 'rellax';

const rellax = new Rellax('.rellax', {
  speed: 2,
  center: true
});

new Glide('.glide', {
  type: 'carousel',
  perView: 4,
  breakpoints: {
      567: {
        perView: 1,
        peek: {
          before: 50,
          after: 50
        }
      }
    }
  
}).mount();

const hamburger = document.querySelector('.hamburger');
const menu = document.querySelector('.nav-menu');
const menuLi = document.querySelectorAll('.nav-menu li')
let isActive = false;

hamburger.addEventListener('click',() => {
  if(!isActive){
    hamburger.classList.add('active');
    isActive = true;
    TweenMax.to(menu, .5, {
      right: '0%',
      opacity: 1,
      ease: Expo.easeInOut
    });
    TweenMax.staggerFrom(menuLi, .65, {
      delay: .1,
      x: 100,
      opacity: 0,
      ease: Expo.easeInOut
    }, .15)
  } else {
    hamburger.classList.remove('active');
    isActive = false;
    TweenMax.to(menu, .3, {
      right: '-100%',
      opacity: 0,
      rotate: 10,
      ease: Expo.easeInOut
    });
    // TweenMax.staggerTo(menuLi, .5, {
    //   x: 100,
    //   opacity: 0,
    //   ease: Expo.easeInOut
    // }, .1)
  }
});